body * {
  margin: 0px;
  padding: 0px;
}

html {
  font-size: 16px;
}

a {
  text-decoration: none;
}

a:hover {
  color: #000;
  transition: all 0.7s ease;
}

avisited {
  color: white;
  text-decoration: none;
}

aselected {
  active: white;
}

.container {
  text-align: center;
  margin: auto;
  padding: 15px;
  max-width: 300px;
  width: 100%;
 /*changes*/ /*changes*/;
}

.container-footer {
  border-top: solid grey 1px;
  border-width: 100%;
  padding: 0;
}

.container-header {
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-style: normal;
  letter-spacing: normal;
}

.container-header .h1header {
  font-size: 1rem;
  padding: 1.25rem;
  color: #fff;
  margin-top: 0;
}

.container-header .h1header a {
  color: white;
}

.container-header .h1header a:hover {
  color: #000;
  transition: all 0.7s ease;
}

.header {
  position: fixed;
  padding: 0px;
  width: 100vw;
  margin: auto;
  text-transform: uppercase;
 /* A partir de aqui es responisve */;
}

.header .group {
  opacity: 0.95;
  background-color: #099d8d;
  text-align: center;
}

.header .main-menu {
  padding: 0.9375rem;
  opacity: 0.9;
  background-color: #14d9c4;
  text-align: center;
}

.header .main-menu ul li {
  display: inline;
}

.header .main-menu ul li a {
  font-size: 0.9375rem;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-style: normal;
  color: #fff;
  padding: 0.5rem;
}

.header .main-menu a:hover {
  color: #000;
  transition: all 0.7s ease;
}

.header .hero {
  background-color: #099d8d;
  background-image: url(../images/IMG_20200207_175627.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 421px;
 /*width: 100vw;*/
  display: flex;
  position: relative;
  z-index: -1;
}

.header .hero .textbox {
  box-sizing: border-box;
  width: 290px;
  height: 80px;
  background-color: #fff;
  color: #000;
  font-size: 1.125rem;
  font-family: "Open Sans", sans-serif;
  position: absolute;
  bottom: -40px;
  left: calc(50% - 145px);
}

.header .hero .textbox div {
  height: 50%;
}

.header .whitebox p {
  position: relative;
  text-align: left;
  padding: 0.9375rem;
}

.aboutus {
  display: flex;
  flex-direction: column;
  margin: 30px 0;
}

.container .ab-header h1 {
  color: #099d8d;
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  text-align: center;
  line-height: 45px;
}

.container .ab-header p {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  text-align: center;
  line-height: 24px;
  color: #54585a;
}/* Fin estilos aboutus */.strengths {
  background-color: rgba(184, 184, 185, 0.2);
  position: relative;
  z-index: -1;
}

.strengths h2 {
  color: #099d8d;
  font-size: 24px;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  margin: 15px;
}

.strengths p {
  padding-bottom: 30px;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  color: #54585a;
  text-align: center;
  line-height: 24px;
}

.triangulo {
  width: 30px;
  height: 30px;
  background-color: white;
  transform: translateX(-50%) rotate(45deg);
  position: absolute;
  top: -15px;
  left: 50%;
}/* Start of Adalabers section style */.adalabers {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 3% 0;
 /* Adalabers profiles icons style */ /* End of Adalabers profiles icons style */;
}

.adalabers-title {
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  color: #099d8d;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  margin: 6% 0 10% 0;
}

.adalabers-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1%;
  margin-bottom: 5%;
}

.adalabers-box {
  display: flex;
  flex-direction: column;
  padding: 2% 3%;
}

.adalabers-name {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  color: #000;
  font-weight: 400;
  margin: 5% 0;
}

.adalabers-description {
  line-height: 1.6;
}

.adalabers p {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #54585a;
  font-weight: 400;
}

.adalabers-pic img {
  max-width: 180px;
  border-radius: 6%;
  border: 4px solid rgba(0, 0, 0, 0.08);
}

.adalabers-pic:hover {
  filter: grayscale(100%);
}

.adalabers .adalabers-links {
  margin-top: 3%;
}

.adalabers .adalabers-links a {
  color: #099d8d;
  display: inline-block;
  padding: 7% 2% 8% 2%;
}

.adalabers .adalabers-links a:visited {
  color: #099d8d;
}

.adalabers .adalabers-links a:hover {
  color: #000;
  transition: all 0.7s ease;
}/* End of Adalabers section */.footer__footer {
  border-top: 1px solid grey;
}

.footer {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  align-content: center;
  color: #b8b8b9;
  font-size: 16px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  border-width: 100%;
  position: relative;
}

.footer .footer-aside {
  margin-bottom: 1%;
}

.footer .footer-nav ul {
  padding: 8px;
}

.footer .footer-nav li {
  list-style-type: none;
  padding: 5px;
}

.footer .footer-nav a {
  color: #54585a;
  font-weight: bold;
  text-decoration: none;
}

.footer .footer-nav a:hover {
  color: #000;
  transition: all 0.7s ease;
}

.footer .footer-logo img {
  width: 120px;
}

.hero-contact {
  background-color: teal;
  background-image: url(../images/contacto.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 421px;
  width: 100vw;
  display: flex;
  position: relative;
  justify-content: center;
  z-index: -1;
}

.main-contact .container {
  text-align: center;
  margin: auto;
  padding: 15px;
  max-width: 300px;
  width: 100%;
}

.main-contact .container h1 {
  color: #099d8d;
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  text-align: center;
  line-height: 45px;
}

.main-contact .container p {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  text-align: center;
  line-height: 24px;
  color: #54585a;
  padding: 15px;
}

.main-contact .container .footer-contact {
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  text-align: center;
  color: #54585a;
  padding: 0px;
  width: 100%;
}

.form {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  text-align: left;
  line-height: 24px;
  color: #54585a;
  padding: 0;
  margin: 0;
  z-index: -3;
}

.container-form {
  margin: auto;
  max-width: 300px;
  width: 100%;
}

label {
  display: block;
  padding: 0;
}

label .asterik {
  color: #14d9c4;
}

input {
  display: block;
  width: 100%;
  padding: 10px 10px;
  margin: 15px 0;
  box-sizing: border-box;
}

textarea {
  display: block;
  width: 100%;
  padding: 10px 10px;
  margin: 15px 0;
  box-sizing: border-box;
}

.button-contact input[type=submit] {
  border-radius: 5%;
  background-color: #099d8d;
  border: 0;
  color: white;
  text-transform: uppercase;
}

@media all and (min-width: 768px) {
  .container {
    max-width: 700px;
  }

  .header .container-header {
    position: fixed;
    width: 100vw;
    background-color: #099d8d;
    opacity: 0.95;
    height: 60px;
  }

  .header .container-header .h1header {
    padding: 1.25rem 0px;
  }

  .header .header {
    position: relative;
    display: flex;
    flex-direction: row;
    opacity: 0.95;
    justify-content: space-between;
    align-items: center;
    max-width: 700px;
  }

  .header .header .main-menu {
    background-color: transparent;
  }

  .header .header .group {
    background-color: transparent;
  }

  .header .hero .whitebox {
    position: relative;
    height: 421px;
  }

  .header .hero .whitebox .textbox {
    left: 15px;
    bottom: 29px;
    opacity: 80%;
    padding: 0.9375rem 1.875rem;
    width: 441px;
  }

  .strengths h2 {
    margin: 0px;
    padding: 15px;
  }

  .strengths p {
    margin: 0 15px;
  }

  .strengths__box {
    display: flex;
  }

  .strengths__box div {
    max-width: 50%;
    padding: 15px 0px;
  }

  .footer {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 'footer-aside' 'footer-nav' 'footer-logo';
  }

  .footer-aside {
    grid-area: footer-aside;
    grid-column: span 1;
  }

  .footer-logo {
    grid-area: footer-logo;
    grid-column: span 1;
    position: absolute;
    left: 3.6%;
    top: -80px;
    z-index: -1;
  }

  .footer-logo img {
    width: 100px;
  }

  .footer-nav {
    grid-area: footer-nav;
    grid-column: 3;
    grid-row: 1;
    text-align: right;
  }

  .button-contact {
    text-align: right;
  }

  .button-contact input[type=submit] {
    width: 200px;
    display: inline;
  }
}

@media (min-width: 768px) {
  .adalabers .adalabers-title {
    margin: 3% 0 6% 0;
  }

  .adalabers .adalabers-profile {
    flex-direction: row;
  }

  .adalabers .adalabers-profile.start {
    text-align: left;
  }

  .adalabers .adalabers-profile.start .adalabers-links {
    margin-left: -0.5%;
  }

  .adalabers .adalabers-profile.end {
    flex-direction: column;
    flex-direction: row-reverse;
    text-align: right;
  }

  .adalabers .adalabers-profile.end .adalabers-links {
    margin-right: 0%;
  }

  .adalabers .adalabers-name {
    margin: 3% 0;
  }

  .adalabers .adalabers.box {
    width: 80%;
  }

  .adalabers .adalabers.box.end {
    text-align: right;
  }

  .adalabers .adalabers-links {
    margin: 0;
  }

  .adalabers .adalabers-links a {
    padding: 5% 1% 0% 1%;
  }

  .adalabers .adalabers-pic img {
    max-width: 207px;
  }
}

@media all and (min-width: 1200px) {
  .container {
    max-width: 600px;
  }

  .container-adalabers {
    width: 100%;
    max-width: 900px;
  }

  .header .header {
    max-width: 900px;
  }

  .header .hero {
    height: 550px;
  }

  .header .hero .whitebox {
    height: 550px;
  }

  .header .hero .whitebox p {
    font-size: 1.25rem;
  }

  .header .hero .whitebox p .textbox {
    width: 574px;
    height: 90px;
  }

  .footer-logo {
    position: absolute;
    left: 6.5%;
  }

  .main-contact .container {
    margin: auto;
    width: 100%;
    max-width: 600px;
  }

  .form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .container-form {
    margin: auto;
    width: 100%;
    max-width: 600px;
  }

  .form input[type="email"] {
    display: inline-block;
    width: 400px;
    margin: 20px 0px 20px 0px;
  }

  .form input[type="tel"] {
    display: inline-block;
    width: 160px;
    margin: 20px 0px;
  }

  .form .email {
    display: inline-block;
    margin-right: 45px;
  }

  .form .tel {
    display: inline-block;
    width: 160px;
    margin-left: 0;
    margin-right: 0;
  }

  .form .container-short {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .form .container-short :first-child {
    order: 1;
  }

  .form .container-short :nth-child(2) {
    order: 3;
  }

  .form .container-short :nth-child(3) {
    order: 2;
  }

  .form .container-short :last-child {
    order: 4;
  }

  .form .button-contact input[type=submit] {
    width: 280px;
  }
}

@media (min-width: 1200px) {
  .adalabers .adalabers-title {
    margin: 0 0 6% 0;
  }

  .adalabers .adalabers-profile {
    align-items: flex-start;
  }

  .adalabers .adalabers-box {
    padding: 0 3%;
    max-width: 50%;
  }

  .adalabers .adalabers-links a {
    padding: 3% 1% 0% 1%;
  }

  .adalabers .adalabers-pic img {
    max-width: 278px;
  }

  .adalabers .start {
    justify-content: left;
  }

  .adalabers .end {
    justify-content: right;
  }
}
